@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.timerCard {
  display: inline-block;
  width: 400px;
  height: 50px;
  padding: 0px;
  margin: 5px;
  border: 1px solid #3F51B5;
  font-size: 14px;
  background-color: white;
  display: flex;
  align-items: center; }
  .timerCard .toggleArea {
    cursor: pointer;
    flex-grow: 1;
    display: flex; }
    .timerCard .toggleArea .pad {
      position: relative;
      left: -1px;
      top: 1px;
      background-color: #3F51B5;
      width: 50px;
      height: 50px;
      z-index: 5; }
    .timerCard .toggleArea .titleArea {
      padding: 5px 10px;
      flex-grow: 1; }
      .timerCard .toggleArea .titleArea .reqSym {
        color: red; }
      .timerCard .toggleArea .titleArea h6 {
        margin-top: 0;
        margin-bottom: 0;
        font-size: 14px; }
      .timerCard .toggleArea .titleArea .date {
        font-size: 10px;
        padding-left: 10px; }
  .timerCard .actionsArea .delete {
    color: red; }

body {
  --ltblue: hsl(209, 100%, 85%);
  --dkgrey: hsl(206, 9%, 15%);
  --ltgrey: hsl(210, 8%, 95%);
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  margin: 0px; }

* {
  box-sizing: border-box; }

#top {
  margin-top: 30px; }

section {
  margin-top: 50px;
  padding-left: 30px;
  max-width: 90vw; }

.basic-info {
  grid-area: basic-info;
  display: grid;
  grid-template: "timer-name timer-name" "timer-category timer-category" "important timer-type" "completed timer-type";
  grid-template-columns: 250px 100px; }
  .basic-info .timer-name-field {
    grid-area: timer-name; }
  .basic-info .timer-category-field {
    margin-bottom: 16px;
    grid-area: timer-category; }
  .basic-info .important-field {
    margin-top: 15px;
    grid-area: important; }
  .basic-info .completed-field {
    grid-area: completed; }
  .basic-info .timer-type-field {
    grid-area: timer-type; }

.specific-info {
  margin-top: 20px;
  grid-area: specific-info; }

.time-input {
  margin-left: 11px; }

.days-input {
  margin-left: 30px;
  display: grid;
  grid-template: ". . . . ." ". . . . .";
  grid-template-columns: 130px 130px 130px 130px 130px; }

input[type="number"] {
  width: 50px; }

.flex {
  display: flex;
  flex-wrap: wrap; }

